html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Space Grotesk", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-size: 2rem;
  background: #303030;
  background: radial-gradient(circle, rgb(48, 48, 48) 0%, rgb(20, 20, 20) 100%);
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
  position: relative;
  overflow: visible;
}

.m-0 {
  margin: 0;
}

a {
  font-size: 2rem;
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

svg {
  max-width: 4rem;
  width: 100%;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}

.swiper-button-prev {
  left: -60px;
}

.swiper-button-next {
  right: -70px;
}

.header {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1200px) {
  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}
.header___titulo {
  color: #fff;
  font-weight: 300;
}
.header___titulo span {
  font-weight: 700;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2.5rem;
  position: relative;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
@media (min-width: 1200px) {
  .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.navegacion__enlace {
  color: #fff;
}
.navegacion__enlace:hover {
  border-bottom: 1px solid red;
}

.slider {
  padding: 5rem 0;
  color: #fff;
}
@media (min-width: 1200px) {
  .slider__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
    padding: 0 10rem;
  }
}
.slider__titulo {
  font-size: 4rem;
  text-align: center;
  font-weight: 700;
}
@media (min-width: 1200px) {
  .slider__titulo {
    font-size: 6rem;
    text-align: left;
  }
}
.slider__parrafo {
  font-weight: 300;
  line-height: 1.3;
  text-align: center;
}
@media (min-width: 1200px) {
  .slider__parrafo {
    text-align: left;
  }
}
.slider__botones {
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  column-gap: 2rem;
  justify-content: center;
}
@media (min-width: 1200px) {
  .slider__botones {
    justify-content: unset;
  }
}
.slider__btn {
  color: #fff;
  font-weight: 700;
  border: 2px solid red;
  padding: 1rem 3rem;
  border-radius: 2rem;
}
.slider__btn:hover {
  background-color: red;
}

.menu {
  padding: 5rem 0;
}
.menu__titulo {
  color: #fff;
  margin-top: 10rem;
}
.menu__titulo::after {
  content: "";
  border-bottom: 2px solid red;
  display: block;
  height: 1rem;
  width: 20rem;
}
@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.menu__imagen {
  height: 20rem;
}
@media (min-width: 768px) {
  .menu__imagen {
    max-width: 30rem;
  }
}
.menu__imagen img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 1rem;
}
.menu__info {
  margin-top: 3rem;
  color: #fff;
}
.menu__info h3 {
  font-size: 2.2rem;
  font-weight: 300;
  margin: 0;
}
.menu__info p {
  margin: 0;
  line-height: 2.5;
  font-weight: 700;
}
.menu__btn {
  color: #fff;
  font-weight: 700;
  border: 2px solid red;
  padding: 1rem 3rem;
  border-radius: 2rem;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  margin: 1.5rem 0;
  display: inline-block;
}
.menu__btn:hover {
  background-color: red;
}

.informacion {
  padding: 5rem 0;
}
.informacion__titulo {
  margin-bottom: 3rem;
  color: #fff;
}
.informacion__titulo::after {
  content: "";
  border-bottom: 2px solid red;
  display: block;
  height: 1rem;
  width: 20rem;
}
.informacion__titulo:last-of-type {
  margin-top: 5rem;
}
.informacion__mapa {
  height: 500px;
  border: 1px solid #fff;
  border-radius: 16px;
  overflow: hidden;
}
.informacion__grid {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .informacion__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.informacion__horas, .informacion__contactos {
  border-top: 1px solid rgb(73.5, 73.5, 73.5);
  color: #fff;
}
.informacion__hora, .informacion__contacto {
  color: #ababab;
  font-weight: 700;
}
.informacion__dias, .informacion__numero {
  font-weight: 300;
}

.footer {
  text-align: center;
  color: #fff;
  font-size: 1.5rem;
}

.cart-container {
  position: absolute;
  top: 160px;
  z-index: 1000;
}
@media (min-width: 768px) {
  .cart-container {
    top: 40px;
    left: 200px;
  }
}
@media (min-width: 1200px) {
  .cart-container {
    top: 40px;
    right: 10px;
    left: unset;
  }
}

.cart-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
  color: #FFF;
}

.cart-dropdown {
  display: none;
  position: absolute;
  width: 400px;
  min-height: 300px;
  left: -185px;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .cart-dropdown {
    width: 500px;
    left: -230px;
  }
}
@media (min-width: 1200px) {
  .cart-dropdown {
    right: -10px;
    top: 30px;
    left: unset;
  }
}

.cart-container:hover .cart-dropdown {
  display: block;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 10px;
}

th, td {
  text-align: left;
  padding: 8px;
  border-bottom: 1px solid #e1e1e1;
}

th {
  color: #fff;
  font-weight: 300;
  font-family: var(--fuente-principal);
}

td {
  color: #fff;
}

td img {
  max-width: 60px;
  height: auto;
  display: block;
}

.empty-cart-btn {
  display: block;
  width: 100%;
  padding: 10px;
  background: white;
  border: 1px solid #e1e1e1;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  color: #262626;
  text-transform: uppercase;
  font-weight: 400;
}

.empty-cart-btn:hover {
  background: #f5f5f5;
}

.borrar-curso {
  background-color: red;
  border-radius: 50%;
  padding: 5px 10px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.formulario {
  padding: 5rem 0;
  display: none;
}
@media (min-width: 768px) {
  .formulario {
    display: block;
  }
}
.formulario__contenedor {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .formulario__contenedor {
    max-width: 90rem;
  }
}
.formulario__fieldset {
  border: 1px solid #e1e1e1;
}
.formulario__legend {
  text-align: center;
  color: #fff;
  font-size: 4rem;
  margin: 0;
}
.formulario__label {
  display: block;
  padding: 1rem 0;
  color: #fff;
}
.formulario__input, .formulario__textarea {
  width: 100%;
  padding: 1rem;
  border: 1px solid #e1e1e1;
  border-radius: 0.5rem;
}
.formulario__textarea {
  height: 20rem;
}
.formulario__botones {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
}
.formulario__boton {
  background-color: #303030;
  border: none;
  color: #fff;
  padding: 1rem 10rem;
  border-radius: 0.5rem;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 700;
}
.formulario__boton:nth-child(1) {
  opacity: 0.5;
  cursor: not-allowed;
}
.formulario__boton:last-of-type {
  background-color: #ababab;
}

.error {
  background-color: red;
  color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  font-weight: 700;
}

.mensaje {
  background-color: green;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.5rem;
  padding: 1rem;
}

.snniper__container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.5rem;
  opacity: 0;
}

.sk-chase {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}

.sk-chase-dot:before {
  content: "";
  display: block;
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  }
  100%, 0% {
    transform: scale(1);
  }
}